<template>
  <div v-show="show"
       class="w-100 h-100 position-fixed"
       style="z-index:9999;margin-top:0px;background-color:rgba(240,240,240,0.5);">
    <button type="button"
            class="close p-2"
            aria-label="Close"
            @click="closeMyPage()">
      <span aria-hidden="true">&times;</span>
    </button>
    <div class="pt-2">
      <form>
        <div class="input-group w-75 m-auto">
          <input type="text"
                 class="form-control"
                 v-model="searchString"
                 placeholder="Name,Tag">
          <div class="input-group-append">
            <button type="button"
                    class="btn btn-primary"
                    @click="search()">{{$t("search")}}</button>
          </div>
        </div>
      </form>
      <div class="list-group list-group-flush m-4 pre-scrollable">
        <div class="list-group-item list-group-item-action"
             v-for="(item, index) in searchResult"
             :key="index">
          <router-link :to="'/article/'+item.id"
                       target="_blank">{{item.name}}</router-link>
          <div>
            <span class=" align-baseline">{{item.createTime}}</span>
            <span v-for="(tag, index) in item.tags"
                  :key="index"
                  class="badge badge-info m-1">{{tag.text}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      show: false,
      articles: [],
      searchString: '',
      searchResult: []
    }
  },
  created () {
    this.reload()
  },
  methods: {
    reload () {
      // this.$http.get('/service/article').then(res => { return res.json() }).then(data => { this.articles = data })
    },
    display () {
      this.show = true
    },
    closeMyPage () {
      this.show = false
    },
    search () {
      this.$http.put('/service/article/search', this.searchString).then(res => {
        return res.json()
      }).then(data => {
        console.log(data)
        this.searchResult = data
      })
    }
  }
}
</script>
<style>
</style>



