<template>
  <div class="d-flex flex-column app-content">
    <div style="height:5rem;">
      <navigator></navigator>
    </div>
    <router-view class="flex-grow-1 d-flex flex-column"></router-view>
    <div class="row m-0 w-100">
      <my-site-footer></my-site-footer>
    </div>
  </div>
</template>
<script>
import navigator from './common/navigator'
import MySiteFooter from './common/MySiteFooter'
import homePage from './homePage/homePage'
import searchArticle from './searchArticle'
export default {
  name: 'mySiteRouter',
  data () {
    return {
      currentComponent: 'home-page'
    }
  },
  created () {
    if (this.$route.path === '/') {
      this.$router.push({ path: 'homePage' })
    }
  },
  components: { navigator: navigator, 'home-page': homePage, 'search-article': searchArticle, 'my-site-footer': MySiteFooter },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



