<template>
  <div class="ml-4 mr-4">
    <div v-for="(article, index) in articles"
         :key="index"
         class="float-left">
      <article-preview :article="article"></article-preview>
    </div>
  </div>
</template>
<script>
import articlePreview from './articlePreview'
export default {
  data () {
    return { articles: [] }
  },
  created () {
    this.reload()
  },
  components: { 'article-preview': articlePreview },
  methods: {
    reload () {
      this.$http.get('/service/recommendArticle').then(res => {
        return res.json()
      }).then(data => {
        this.articles = data
      })
    }
  }
}
</script>



