<template>
  <div class=" card card-body float-left w-50">
    <h4 class=" align-self-start">{{$t("related_article")}}</h4>
    <ul class=" list-group">
      <li class=" list-group-item"
          v-for="(article, index) in relatedArticles"
          :key="index"
          style="height:5rem;">
        <h5 class="align-self-start d-flex">
          <router-link :to="'/article/'+article.id"
                       target="_blank">{{article.name}}</router-link>
        </h5>
        <p v-html="article.description"
           class="text-truncate"></p>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data () {
    return { relatedArticles: [] }
  },
  props: {
    article: {
      type: Object,
      required: true
    }
  },
  watch: {
    article (newArticle, oldArticle) {
      this.reload()
    }
  },
  methods: {
    reload () {
      this.$http.put('/service/article/related', this.article).then(res => {
        return res.json()
      }).then(data => {
        this.relatedArticles = data
      })
    }
  }
}
</script>
<style>
</style>



