<template>
  <div class="card animated slideInUp faster"
       style="width:25rem;max-width:100%;max-height:12rem;">
    <div class=" card-body text-left text-justify h-50"
         style="text-overflow: ellipsis;overflow : hidden;">
      <h5 class="card-title">
        <router-link :to="'/article/'+article.id"
                     target="_blank">{{article.title}}</router-link>
      </h5>
      <span style="font-size:12px;"
            class=" align-baseline">{{article.time}}</span>
      <span style="font-size:12px;"
            class="badge badge-info m-1"
            v-for="(item, index) in article.tags"
            :key="index">{{item.text}}</span>
      <p class="card-text h-50">{{article.description}}</p>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {}
  },
  props: {
    article: Object
  }
}
</script>



