<template>
  <div>
    <div id="lv-container"
         data-id="city"
         :data-uid="uid">
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      uid: ''
    }
  },
  props: {
    dataId: String
  },
  mounted () {
    var config = this.$sysConfig
    this.uid = config.comment.liveRe.uid
    this.init()
  },
  methods: {
    init () {
      ; (function (d, s) {
        var j = d.getElementsByTagName(s)[0]
        var e = d.getElementsByTagName(s)[0]

        if (typeof LivereTower === 'function') {
          return
        }

        j = d.createElement(s)
        j.src = 'https://cdn-city.livere.com/js/embed.dist.js'
        j.async = true

        e.parentNode.insertBefore(j, e)
      })(document, 'script')
    }
  }
}
</script>



