<template>
  <div>
    <div class="card card-body m-3">
      <h5>{{article.name}}</h5>
      <div><span class="badge badge-secondary mr-1">{{article.createTime}}</span></div>
      <div><span class="badge badge-secondary mr-1"
              v-for="(tag, index) in article.tags"
              :key="index">{{tag.text}}</span></div>
      <div>
        <p class="text-left m-2"
           v-html="article.context"></p>
      </div>
    </div>
    <div class="p-3">
      <related-article :article="article"></related-article>
      <recommond-article></recommond-article>
    </div>
    <comment dataId='test'
             class="bg-white"></comment>
  </div>
</template>
<script>
import recommondArticle from './recommondArticle'
import relatedArticle from './relatedArticle'
import comment from './common/commentLiveRe.vue'
import navigator from './common/navigator'
export default {
  data () {
    return {
      article: {}
    }
  },
  created () {
    this.reload()
  },
  components: { navigator: navigator, comment, relatedArticle, recommondArticle },
  methods: {
    reload () {
      if (this.$route.params === undefined || this.$route.params.id === undefined) {
        this.$router.push({ path: '/error', query: { type: '404' } })
      } else {
        this.$http.get('/service/article/' + this.$route.params.id).then(res => {
          return res.json()
        }, res => {
          this.$router.push({ path: '/error', query: { type: '404' } })
        }).then(data => {
          this.getArticle(data.id, text => {
            this.article = data
            this.article.context = text
          })
        })
      }
    },
    getArticle (id, cb) {
      this.$http.get('./static/articles/' + id + '.html').then(res => {
        return res.text()
      }).then(data => {
        cb(data)
      })
    }
  }
}
</script>
<style>
</style>



