<template>
  <div>
    <nav class="navbar navbar-expand-lg navbar-light bg-white position-fixed w-100"
         style="z-index:999;">
      <div class="navbar-header">
        <router-link class="navbar-brand"
                     to="/homePage">
          <img style="width: 5rem;"
               src="./../../../static/img/brand.png" />
        </router-link>
        <button class="navbar-toggler"
                type="button"
                data-toggle="collapse"
                data-target="#navbarToggleExternalContent"
                aria-controls="navbarToggleExternalContent"
                aria-expanded="false"
                aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
      </div>
      <div class="collapse navbar-collapse"
           id="navbarToggleExternalContent">
      </div>
      <form class="form-inline">
        <ul class="list-inline align-items-baseline mb-0 border-right pr-3">
          <li class="list-inline-item">
            <button class="btn btn-outline-dark border-0 iconfont icon-search"
                    @click="searchPage('search-article')">
            </button>
          </li>
          <li class="list-inline-item">
            <button class="btn btn-outline-dark border-0 iconfont icon-setting"
                    @click="settings()">
            </button>
          </li>
          <li class="list-inline-item">
            <button class="btn btn-outline-dark border-0 iconfont icon-xiaoxi1"
                    @click="message()">
            </button>
          </li>
        </ul>
        <img class=" rounded-circle align-items-baseline ml-3"
             @click="openMyPage()"
             style="height:2.5rem;"
             src="./../../../static/img/myIcon.png">
        <button class="btn btn-outline-dark border-0 iconfont icon-app ml-1"
                style="font-size:1.5rem;"
                @click="toggleAppList">
        </button>
        <!-- <i class="iconfont icon-app ml-3 position-relative"
           style="font-size:1.5rem;"
           @click="toggleAppList"></i> -->
        <i class="position-relative">
          <navigator-app-list :class="appListShowClass"
                              v-show="appListShow"></navigator-app-list>
        </i>
      </form>
    </nav>
    <my-page ref="myPage"></my-page>
    <search-article ref="searchArticle"></search-article>
    <settings ref="settings"></settings>
  </div>
</template>
<script>
import myPage from './../myPage'
import searchArticle from './../searchArticle'
import settings from './../settings'
import NavigatorAppList from './NavigatorAppList'
export default {
  data () {
    return { navs: [], appListShow: false, appListShowClass: '' }
  },
  components: { 'my-page': myPage, 'search-article': searchArticle, settings, NavigatorAppList },
  created () {
    this.navs = [{ text: 'Home', code: 'home-page' }]
  },
  methods: {
    searchPage (pageName) {
      this.$refs.searchArticle.display()
    },
    openMyPage () {
      this.$refs.myPage.display()
    },
    settings () {
      this.$refs.settings.modal()
    },
    message () {
      this.$msgBox.toggle()
    },
    toggleAppList () {
      this.appListShow = !this.appListShow
      if (this.appListShow) {
        this.appListShowClass = 'slideInRight'
      } else {
        this.appListShowClass = ''
      }
    }
  }
}
</script>



