<template>
  <div class=" card card-body float-left w-50">
    <h4 class="align-self-start">{{$t("recommond_article")}}</h4>
    <ul class=" list-group">
      <li class=" list-group-item"
          v-for="(article, index) in recommondArticles"
          :key="index"
          style="height:5rem;">
        <h5 class="align-self-start d-flex">
          <router-link :to="'/article/'+article.code"
                       target="_blank">{{article.title}}</router-link>
        </h5>
        <p v-html="article.description"
           class="text-truncate"></p>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data () {
    return {
      recommondArticles: []
    }
  },
  created () {
    this.reload()
  },
  props: {},
  methods: {
    reload () {
      this.$http.get('/service/recommendArticle').then(res => {
        return res.json()
      }).then(data => {
        this.recommondArticles = data
      })
    }
  }
}
</script>



