import Vue from 'vue'
import Router from 'vue-router'
import mySiteRouter from '@/components/mySiteRouter'
import HomePage from '@/components/homePage/homePage'
import article from '@/components/article'
import error from '@/components/common/error'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'mySiteRouter',
      component: mySiteRouter,
      children: [{
        path: 'article/:id',
        component: article
      }, {
        path: 'homePage',
        component: HomePage
      }]
    },
    {
      path: '/error',
      name: 'error',
      component: error
    }
  ]
})



